.topbar{
	display: none;
	padding: .25rem 0;
	background-color: $secondary;
	
	@include media-breakpoint-up(md){
		display: block;
	}
	@include media-breakpoint-up(xl){
		padding: .5rem 0;
	}
	
	.topbar-list-contact,
	.topbar-list-social{
		@include list-unstyled();
		margin-bottom: 0;
		
		@include media-breakpoint-up(md){
			display: flex;
		}
		
		li{
			a{
				color: $white;
				text-decoration: none;
				
				&:hover{
					i{
						color: $primary;
					}
				}
				
				i{
					transition: color ease-in-out .3s;
				}
			}
		}
	}
	
	.topbar-list-contact{
		@include media-breakpoint-up(md){
			flex-direction: row;
		}
		
		li{
			@include media-breakpoint-up(md){
				margin-right: 2rem;
			}
			
			a{
				font-size: 1rem;
			}
		}
	}
	
	.topbar-list-social{
		display: flex;
		flex-direction: row-reverse;
		
		li{
			margin-left: 1rem;
		}
	}
}

.navbar{
	width: 100%;
	padding: .25rem 0;
	
	@include media-breakpoint-up(xxl){
		padding: .5rem 0;
	}
	
	&.navbar-main{
		position: static;
		width: 100%;
		margin-bottom: 2rem;
		z-index: 1.5;
		background-color: $white;
		
		@include media-breakpoint-up(lg){
			margin-bottom: 3rem;
		}
	}
	
	.navbar-inner{
		display: flex;
	  	flex-wrap: inherit;
	  	align-items: center;
	  	justify-content: space-between;
		width: 100%;
		padding: 1rem 0;
		border-bottom: solid 1px rgba($scorpion, .15);
		
		@include media-breakpoint-up(lg){
			padding: 2rem 0;
		}
		
	}
	
	&.navbar-reveal{
		position: fixed;
		top:-300px;
		width: 100%;
		z-index: 100;
		background-color: rgba($white, .75);
		box-shadow: 0 5px 15px 0 rgba($black, 0);
		transition: all ease-in-out .65s;
		backdrop-filter: blur(8px);
		
		.navbar-brand{
			//padding: .25rem 0;
			max-width: 250px;
		}
		
		&.affix{
			top: 0;
			box-shadow: 0 5px 15px 0 rgba($black, .25);
		}
		
		.nav-item-btn{
			display: none!important;
		}
	}
	
	.navbar-toggler{
		border: none;
		border-radius: 0;
		box-shadow: none!important;
		color: $text;
	}
	
	.navbar-brand{
		padding: 0;
		max-width: 200px;
		transition: all ease-in-out .3s;
		
		@include media-breakpoint-up(md){
			max-width: 300px;
		}
		
		@include media-breakpoint-up(xxl){
			max-width: 370px;
		}
	}
	
	.navbar-collapse{
		margin-top: .5rem;
		
		@include media-breakpoint-up(lg){
			margin-top: 0;
		}
	}
	
	.navbar-nav{
		@include media-breakpoint-up(lg){
			width: 100%;
		}
	}
	
	.nav-item{
		.nav-link{
			padding: .75rem;
			border-top: solid 1px $neutral;
			text-transform: uppercase;
			font-size: 1rem;
			color: $text;
			transition: padding ease-in-out .3s,
						background-color ease-in-out .3s;
			
			@include media-breakpoint-up(lg){
				padding: .75rem;
				border-top: none;
			}
			
			@include media-breakpoint-up(xl){
				padding: .4rem 1.25rem;
			}
			
			@include media-breakpoint-up(xxl){
				//font-size: 1.1rem;
			}
		}
		
		&:first-child{
			@include media-breakpoint-up(lg){
				margin-left: auto;
			}
		}
		
		&:not(:last-child){
			
		}
		
		.dropdown-menu{
			margin-bottom: .75rem;
			border: none;
			border-radius: 0;
			background-color: transparent;
			
			@include media-breakpoint-up(lg){
				margin-bottom: 0;
				border: none;
				border-radius: 0 0 .375rem .375rem;
				background-color: $white;
			}
			
			@include media-breakpoint-up(xxl){
				top: 103%;
			}
			
			.nav-item{
				.dropdown-item{
					font-size: .875rem;
					color: $text;
					
					&.active,
					&:active,
					&:hover,
					&:focus{
						color: $primary;
						background-color:#e9ecef
					}
					
					@include media-breakpoint-up(xxl){
						//font-size: 1rem;
					}
				}
			}
		}
		
		&.nav-item-btn{
			display: none;
			
			@include media-breakpoint-up(xl){
				display: flex;
			}
			
			.nav-btn{
				font-size: .875rem;
				color: $white;
				
				@include media-breakpoint-up(lg){
					padding: .5rem 1.35rem
				}
				
				@include media-breakpoint-up(xl){
					font-size: 1rem;
				}
				
				@include media-breakpoint-up(xxl){
				}
			}
			
		}
	}
}